@use "global" as *;

.p-page-about__section-title {
  text-align: center;
}

.p-page-about__contents {
  margin-top: rem(40);

  @include mq(md) {
    margin-top: rem(60);
  }
}

.p-page-about__items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: rem(11);

  @include mq(md) {
    grid-template-columns: repeat(3, 1fr);
    gap: rem(24);
  }
}